@mixin desktop {
  @media all and (min-width: 1024px) {
    @content;
  }
}

@mixin inner() {
  @include desktop() {
    width: 1024px;
    margin: 0 auto;
  }
}

@mixin onlyDesktop($displayType: block) {
  display: none;

  @include desktop {
    display: $displayType;
    @content;
  }
}

@mixin onlyMobile($displayType: block) {
  display: $displayType;
  @content;

  @include desktop() {
    display: none;
  }
}

@mixin centerRow() {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin centerColumn() {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@mixin h1() {
  color: $color_primary_dark;
  font-size: 24px;
  letter-spacing: -0.7px;

  @include desktop() {
    font-size: 36px;
  }
}

@mixin button() {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}