@import "../../assets/scss/app.scss";

.app {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  &_fixed {
    height: 100vh;
    overflow: hidden;
  }

  main {
    flex-grow: 1;
    background-image: url(../../assets/jpeg/back.jpg);
    background-repeat: repeat;

    .inner {
      @include inner();
      padding: 16px;

      @include desktop() {
        padding: 24px 0;
      }
    }
  }
}